$color-pink: #F76C6C;
$color-pink-bold: #FF4C6C;
$color-paragraph: #444954;
$color-gray: #777777;
$color-headings: #333333;

#content {
  float: left;
  padding: 0;
  width: 68%;
}

/* Contatiner part */
#container {
  display: flex;
  justify-content: space-between;
  margin: 0 10px;
  margin-bottom: 20px;
}

.aside {
  float: right;
  width: 30%;
}

.box-img-title {
  margin-bottom: 20px;
}

.img-title {
  border-radius: 10px 10px 0 0;
  max-height: 350px;
  object-fit: cover;
  object-position: 0 30%;
  width: 100%;
}

.box-content_title {
  margin: auto;
  margin-bottom: 20px;
  width: 100%;
}

.content img {
  border-radius: 5px;
  display: block;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  max-width: 75%
}

/* line */
.line-dash {
  border: 1px dashed #999999;
}

/* text */
.fix-date{
  padding-left: 30px;
}

.txt-main {
  color: #333333;
  text-indent: 10px;
  margin-top: 0;
}

/* Headings */
#content h1 {
  font-size: 30px;
  color: $color-pink;
  margin-bottom: 0.3em;
}

#content h2 {
  margin-top: 2em;
  color: $color-headings;
  font-size: 25px;
}

#content h3 {
  margin-top: 2em;
  color: $color-headings;
  font-size: 20px;
}

#content h4 {
  margin-top: 2em;
  color: $color-headings;
  font-size: 15px;
}

#content p {
  color: $color-paragraph;
  font-size: 17px;
  font-weight: 400;
  line-height: 1.8em;
  text-indent: 1em;
  margin-block-end: 1.5em;
}

#content li {
  color: $color-paragraph;
  font-size: 17px;
  font-weight: 400;
  line-height: 1.8em;
}

#content p a {
  color: $color-pink;
}

#content p a:hover {
  color: $color-pink-bold;
}

#content strong {
  background-color: #F4F4F4;
  color: $color-pink;
  font-weight: bolder;
  letter-spacing: 0.01em;
  padding: 0 0.5em;
}

#content time {
  color: $color-gray;
}

.far {
  padding-right: 0.5em;
}

p em {
  display: block;
  color: $color-gray;
  text-align: center;
  font-style: normal;;
}